.mn-sidebar {
    float: left;
    position: relative;
    z-index: 99;
}
.mn-sidebar >.handler {
    float: right;
    margin-right: -29px;
    background-color: #fff;
    padding: 12px;
    border-radius: 0px 5px 5px 0px;
    margin-top: 10px;
    cursor: pointer;
    
}
.mn-sidebar.hide > .sidebar-panel {
    width:0px;
}
.mn-sidebar.hide > .sidebar-panel > .content {
    display: none;
}
.mn-sidebar .sidebar-panel {
    height: 97vh;
    width: 400px;
    box-shadow: 4px 20px 11px #000;
    transition: width .5s;
    transition-timing-function: linear;
    
}